import { users, getOpenid, get_phoneLogin } from '@/http/api.js';
export default {
	data() {
		return {
			id: '',
			isDis: '',
			uid: '',
			data: '',
			code: '',
			userInfo: [],
			openid: '',
			session_key: ''
			// openidUrl: 'https://api.weixin.qq.com/sns/jscode2session', //获取openid接口
			// APP_SECRET: 'ddec28ff357d206e32a42b41575de883',
			// APP_ID: 'wx620e699d34566854'
		};
	},
	onShow() {},
	onLoad(options) {
		// this.getConfig();
		this.id = options.id;
		this.isDis = options.isDis;
		this.uid = options.uid;

		console.log(this.id);
	},
	methods: {
		//获取用户信息
		getConfig(e) {
			console.log(e);
			// return false;
			let auth_param = e.detail;
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes);
					that.code = loginRes.code;
					uni.setStorageSync('code', loginRes.code);
					getOpenid(loginRes.code).then(res => {
						if (res.code == 0) {
							that.openid = res.data.openid;
							uni.setStorageSync('openid', res.data.openid);
							that.session_key = res.data.session_key;
							uni.setStorageSync('session_key', res.data.session_key);
							uni.getSetting({
								success(res) {
									if (res.authSetting['scope.userInfo']) {
										uni.getUserInfo({
											success: function(infoRes) {
												that.userInfo = infoRes.userInfo;
												uni.setStorageSync('userInfo', infoRes.userInfo);
												// uni.navigateTo({
												// 	url: '/pages/studio/studio?id=' + that.id
												// });
											}
										});
									}
								}
							});
							get_phoneLogin({
								code: loginRes.code,
								encryptedData: auth_param.encryptedData,
								iv: auth_param.iv,
								openid: that.openid,
								session_key: that.session_key
							}).then(res => {
								console.log(res);
								if (res.token) {
									uni.setStorageSync('token', res.token);
								}
								let pages = getCurrentPages();
								let prevPage = pages[pages.length - 2]; //上一个页面
								//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
								prevPage.setData({
									id: that.id,
									isDis: that.isDis,
									uid: that.uid
								});
								wx.navigateBack({
									//返回
									delta: 1
								});
							});
							// uni.getSetting({
							// 	success(res) {
							// 		if (res.authSetting['scope.userInfo']) {
							// 			uni.getUserInfo({
							// 				success: function(infoRes) {
							// 					that.userInfo = infoRes.userInfo;
							// 					uni.setStorageSync('userInfo', infoRes.userInfo);
							// 					// uni.navigateTo({
							// 					// 	url: '/pages/studio/studio?id=' + that.id
							// 					// });
							// 				}
							// 			});
							// 		}
							// 	}
							// });
						}
					});
				}
			});
		}
	}
};
